<template>
  <div class="blog">
    <div class="sections">
      <div :class="searchclass">
        <div class="searbox">
          <el-input placeholder="输入关键字搜索！" prefix-icon="el-icon-search" v-model="search"></el-input>
          <el-collapse-transition>
            <div class="linkword" v-show="linkword.length!==0">
              <div class="padtop"></div>
              <li v-for="word in linkword" :key="word.blogId">
                <span @click="readblog(word.blogId)">{{word.title}}</span>
              </li>
            </div>
          </el-collapse-transition>
        </div>
        <div class="typelist">
          <li
            :class="pertype===type?'active':''"
            v-for="(pertype,index) in typelist"
            :key="index"
            @click="toogletype(pertype)"
          >{{pertype}}</li>
        </div>
      </div>
      <div class="top">
        <div class="tophead">热门文章</div>
        <li
          v-for="(top,index) in toplist"
          class="topitem"
          :key="top.blogId"
          @click="readblog(top.blogId)"
        >
          <span class="num" :class="mapclass(index)">{{index+1}}</span>
          <span class="title">{{top.title}}</span>
        </li>
      </div>
    </div>
    <div
      class="bloglist"
      infinite-scroll-distance="100px"
      v-infinite-scroll="loadblog"
      :infinite-scroll-disabled="disabled"
      infinite-scroll-immediate="false"
    >
      <div
        class="item"
        v-for="(blog,index) in bloglist"
        :key="blog.Id"
        :style="{animationDelay:(index%blognum)/5+'s'}"
      >
        <div class="Bhead" @click.prevent.stop="readblog(blog.blogId)">
          <span class="title">{{blog.title}}</span>
          <div class="showdetail">
            <span class="readnum num">
              <span class="el-icon-view" style="marginRight:5px;"></span>
              <span class="text">{{blog.readnum}}</span>
            </span>
            <span class="comnum num">
              <span class="el-icon-chat-line-round" style="marginRight:5px;"></span>
              <span class="text">{{blog.comnum}}</span>
            </span>
          </div>
        </div>
        <div class="Bbody">
          <el-image :src="blog.avatar" :preview-src-list="[blog.avatar]" fit="fill">
            <div slot="placeholder" class="image-slot">
              <i class="el-icon-loading"></i>
            </div>
          </el-image>
          <div class="descript">{{blog.description}}</div>
        </div>
        <div class="Bfoot">
          <div class="msgbox">
            <span class="mark">
              <span class="el-icon-price-tag icon"></span>
              <el-tag class="tag" type="info" effect="dark" size="small">{{blog.essaymark}}</el-tag>
            </span>
            <span class="created_time">
              <span class="el-icon-time"></span>
              {{blog.created_time | dateformat(blog.created_time)}}
            </span>
          </div>
        </div>
        <Randomimg />
      </div>
      <p v-if="!disabled" class="scroll-tip">
        <span class="el-icon-loading"></span> 加载中...
      </p>
      <p v-if="disabled" class="scroll-tip">没有更多了</p>
    </div>
  </div>
</template>

<script>
import Randomimg from "@/components/randomImg";
import {
  getbloglist,
  getbloginit,
  getlinkword,
  getlistbytype
} from "@/api/index.js";
export default {
  name: "blog",
  data() {
    return {
      offblog: 0,
      blognum: 5,
      bloglist: [],
      typelist: ["全部文章"],
      toplist: [],
      search: "",
      disabled: false,
      searchclass: "search",
      linkword: [],
      bloglistnum: -1,
      type: "全部文章"
    };
  },
  mounted() {
    this.getBlogList({ offblog: this.offblog, blognum: this.blognum });
    this.getBlogInit();
    window.addEventListener("scroll", this.scrollhandel, true);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollhandel, true);
  },
  components: { Randomimg },
  methods: {
    getBlogList(params) {
      getbloglist(params).then(res => {
        if (res.data.code === 200) {
          this.bloglist = this.bloglist.concat(res.data.data.list);
          this.bloglistnum = res.data.data.listnum;
          this.scrollloadcontrol();
        }
      });
    },
    getBlogInit() {
      getbloginit({}).then(res => {
        let result = res.data;
        if (result.code === 200) {
          this.typelist = this.typelist.concat(result.data.type);
          this.toplist = result.data.top;
        }
      });
    },
    getLinkWord() {
      if (this.search.trim().length <= 0) {
        this.clearSear();
        return;
      }
      let params = { linkword: this.search };
      getlinkword(params).then(res => {
        this.linkword = res.data.data;
      });
    },
    scrollhandel() {
      let t0 = $(".top").offset().top;
      if (t0 < -451) {
        this.searchclass = "search1";
      } else {
        this.searchclass = "search";
      }
    },
    clearSear() {
      this.$nextTick(() => {
        this.search = "";
        this.linkword = [];
      });
    },
    mapclass(value) {
      switch (value) {
        case 0:
          return "first";
          break;
        case 1:
          return "second";
          break;
        case 2:
          return "three";
          break;
        default:
          return "";
          break;
      }
    },
    loadblog() {
      this.offblog = this.offblog + 1;
      if (this.bloglistnum > this.blognum * this.offblog) {
        let params = this.formatParams();
        this.getBlogList(params);
      }
    },
    toogletype(title) {
      this.offblog = 0;
      this.bloglist = [];
      this.type = title;
      let params = this.formatParams();
      this.getBlogList(params);
    },
    formatParams(type) {
      if (this.type === "全部文章") {
        return { offblog: this.offblog, blognum: this.blognum };
      } else {
        return {
          offblog: this.offblog,
          blognum: this.blognum,
          type: this.type
        };
      }
    },
    scrollloadcontrol() {
      if (this.bloglistnum > this.blognum * (this.offblog + 1)) {
        this.disabled = false;
      } else {
        this.disabled = true;
      }
    },
    readblog(blogId) {
      this.$router.push({ name: "blogreading", params: { blogId: blogId } });
    }
  },
  filters: {
    dateformat: function(time) {
      return time.slice(0, 10);
    }
  },
  watch: {
    search() {
      this.getLinkWord();
    }
  }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/index.scss";
@import "../assets/css/animation.css";
@media screen and (min-width: 500px) {
  .Bbody .el-image {
    width: 300px;
    height: 180px;
    margin: 0 10px 10px 10px;
    float: left;
    clear: both;
  }
}
@media screen and (max-width: 500px) {
  .el-image {
    display: block;
    width: 100%;
    max-width: 300px;
    height: 180px;
    margin: 0 auto 10px;
  }
}
@media screen and (max-width: 885px) {
  .sections {
    display: none;
  }
  .el-image {
    width: 100%;
  }
}
.blog {
  width: 90%;
  max-width: 1200px;
  margin: 20px auto;
  display: flex;
  z-index: 1;
  & .sections {
    flex-basis: 280px;
    margin-top: 20px;
    @include fontnor_20;
    & .searbox {
      padding: 15px;
      min-height: 35px;
      @include background(#808080, 0.9);
      & .el-input {
        height: 35px;
      }
      & .linkword {
        @include fontxing_14;
        margin: 0 auto;
        @include background($background-color-theme);
        z-index: 0;
        border-radius: 5px;
        margin-top: -19px;
        padding-left: 10px;
        & .padtop {
          width: 100%;
          height: 30px;
          @include background($background-color-theme);
        }
        & li {
          height: 25px;
          line-height: 25px;
          padding-left: 10px;
          animation: fadeIn 0.3s backwards;
          @include background($background-color-theme);
          &:last-child {
            padding-bottom: 5px;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
          }
        }
      }
    }
    & .typelist {
      @include background($background-color-theme, 0.9);
      & li {
        height: 50px;
        line-height: 50px;
        margin: 0 20px;
        padding-left: 20px;
        border-bottom: 1px dashed rgba(220, 220, 220, 0.9);
        @include color($font-color-shallow6);
        @include fontnor_12;
        &:last-child {
          border-bottom: none;
        }
        &.active {
          @include font1_20;
          @include color($font-color-theme);
          border-bottom: 2px solid rgba(123, 126, 127, 0.9);
          background: rgba(250, 237, 217, 0.2);
          animation: flipInX 0.5s backwards;
        }
        &:hover {
          background: rgba(250, 237, 217, 0.2);
        }
      }
    }
    & .top {
      @include fontnor_16;
      margin-top: 20px;
      @include background($background-color-theme, 0.9);
      animation: 0.3s slideInLeft 0.5s backwards;
      & .tophead {
        @include color($font-color-shallow3);
        height: 50px;
        line-height: 50px;
        text-align: center;
        border-bottom: 1px solid #ccc;
        font-weight: 800;
        letter-spacing: 1px;
      }
      & .topitem {
        height: 40px;
        line-height: 40px;
        padding: 0 20px 0;
        @include fontnor_12;
        @include color($font-color-shallow6);
        & .num {
          display: inline-block;
          width: 18px;
          height: 18px;
          line-height: 18px;
          border-radius: 50%;
          font-family: auto;
          @include color($font-color-shallow6);
          @include background(#fff);
          &.first {
            @include background(#febcbc);
          }
          &.second {
            @include background(#fefebc);
          }
          &.three {
            @include background(#bcfebc);
          }
          vertical-align: middle;
          text-align: center;
          @include fontnor_12;
        }
        & .title {
          padding-left: 10px;
        }
      }
    }
  }
  & .bloglist {
    flex: 1;
    @include fontnor_16;
    padding: 10px;
    box-sizing: border-box;
    & .item {
      position: relative;
      min-width: 260px;
      min-height: 280px;
      margin: 10px auto;
      @include background($background-color-theme, 0.9);
      border-radius: 5px;
      box-shadow: 0 1px $background-color-theme 2px 5px outset;
      animation: slideInRight 0.5s backwards;
      & .Bhead {
        position: relative;
        height: 50px;
        line-height: 50px;
        padding-bottom: 20px;
        &::before {
          content: "";
          @include background(#ccc);
          height: 1px;
          position: absolute;
          left: 20px;
          right: 120px;
          bottom: 20px;
        }
        & .title {
          @include color($font-color-shallow3);
          @include font1_26;
          padding-left: 20px;
          &:hover {
            @include color($font-color-shallow6);
          }
        }
        & .showdetail {
          display: inline-block;
          position: absolute;
          right: 20px;
          bottom: -7px;
          font-family: auto;
          & .num {
            padding: 5px;
            @include color($font-color-shallow9);
          }
          & .text {
            @include fontnor_12;
          }
        }
      }
      & .Bbody {
        min-height: 180px;
        & .el-image .image-slot {
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          border: 1px solid rgb(172, 166, 166);
          box-sizing: border-box;
          & i {
            @include divabcenter;
            @include fontnor_20;
            @include color($font-color-shallow9);
          }
        }
        & .descript {
          display: block;
          @include color($font-color-shallow9);
          @include fontnor_12;
          line-height: 1.5;
          letter-spacing: 1px;
          white-space: normal;
          padding: 0 10px 0;
        }
      }
      & .Bfoot {
        display: block;
        height: 40px;
        line-height: 40px;
        width: 100%;
        margin-top: 10px;
        position: relative;
        @include fontnor_20;
        @include color($font-color-shallow9);
        &::before {
          content: "";
          position: absolute;
          height: 1px;
          right: 0;
          left: 250px;
          bottom: 15px;
          @include background(#ccc);
        }
        & .msgbox {
          position: absolute;
          left: 10px;
          & .icon {
            transform: rotate(-45deg);
            padding: 5px;
          }
          & .tag {
            margin-left: 5px;
          }
        }
        & .created_time {
          padding-left: 30px;
          @include fontnor_12;
        }
      }
    }
  }
  & .scroll-tip {
    height: 40px;
    line-height: 40px;
    text-align: center;
    // color: #999;
    // font-family: "shuxing";
    @include color($font-color-shallow9);
    @include fontnor_12;
  }
}
.sections .search {
  position: relative;
  animation: slideInLeft 0.5s backwards;
}
.sections .search1 {
  width: 280px;
  animation: 1s slideInLeft 1s backwards;
  position: fixed;
  top: 80px;
  z-index: 0;
}
.sections .searbox /deep/ .el-input__inner {
  border-radius: 20px;
  height: 35px;
  line-height: 35px;
  @include fontxing_14;
  font-weight: 600;
  outline: none;
  border: none;
}
.sections .searbox /deep/ .el-input__inner:focus {
  outline: none;
  border: none;
}
.sections .searbox /deep/ .el-input__icon {
  line-height: 35px;
}
</style>