<template>
  <div class="randomimg">
     <img width="80" height="80" :src="require('../assets/imgs/random'+param+'.png')" alt />
  </div>
</template>

<script>
export default {
  name: "randomimg",
  data() {
    return {
      param:4,
    };
  },
  created() {
    this.param = ~~(Math.random()*4) + 1
  }
};
</script>

<style scoped>
.randomimg {
  position: absolute;
  right: -10px;
  bottom: -10px;
  width: 80px;
  height: 80px;
}
</style>